
// インナーサブ
.l-inner-sub {
	margin: 0 auto;
	max-width: rem(540); //状況によって変える必要があるかも
	width: 100%;
	height: inherit;
	padding-right: $padding-sp;
	padding-left: $padding-sp;
	@include mq(tab) {
    max-width: 688px + strip-unit($padding-pc) * 2;
		padding-right: $padding-pc;
		padding-left: $padding-pc;
	}

}